@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700;1,400&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}

* {
    box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  color: #999999;
  color: var(--neutral-grey-two)
}

:root {
  --primary: #FF6600;
  --accent-red: #F5515F;
  --neutral-light: #FFF0E6;
  --neutral-grey-one: #EDF0F3;
  --neutral-grey-two: #999999;
  --neutral-dark: #333333;
}

.app{text-align:center}

.quantity-rocker{display:flex}.quantity-rocker button:first-child{border-top-left-radius:2px;border-bottom-left-radius:2px}.quantity-rocker button:last-child{border-top-right-radius:2px;border-bottom-right-radius:2px}.rocker-button{display:flex;width:40px;height:40px;background-color:#FFFFFF;align-items:center;justify-content:center;padding:0;font-size:28px;color:var(--primary);border:1px solid var(--primary);transition:0.1s}.rocker-button:hover{cursor:pointer;border:2px solid var(--primary)}.rocker-button:active{color:var(--accent-red);border:3px solid var(--accent-red)}.rocker-button:disabled{border:1px solid #E6E7EB;color:#E6E7EB}.rocker-input{width:40px;height:40px;text-align:center;border-radius:0;border:none;box-shadow:inset 0 1px 0 0 #E6E7EB, inset 0 -1px 0 0 #E6E7EB}.rocker-input::-webkit-outer-spin-button,.rocker-input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}.rocker-input[type=number]{-moz-appearance:textfield}@media (min-width: 1320px){.rocker-input{width:80px}}

.button{display:flex;grid-gap:12px;gap:12px;width:100%;height:40px;justify-content:center;align-items:center;line-height:20px;font-family:inherit;font-weight:500;border:1px solid var(--primary);border-radius:20px;transition:0.1s}.button:hover{cursor:pointer}.button:active{-webkit-transform:scale(0.98);transform:scale(0.98)}.orange{background-color:var(--primary);color:#FFFFFF}.orange:hover{border:3px solid var(--neutral-light)}.white{background-color:#FFFFFF;color:var(--primary)}.white:hover{border:3px solid var(--primary)}

.spinner{margin:40px auto;height:150px}.main-section{display:grid;grid-template-rows:repeat(auto-fit, minmax(360px, 1fr));grid-gap:12px;width:360px;margin:0 auto;justify-content:center}@media (min-width: 1320px){.main-section{grid-template-columns:416px 526px 306px;grid-gap:24px;margin:48px auto}}@media (min-width: 1320px){.image-section{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}}.image{width:100%;height:100%}.description-section{display:grid;grid-gap:12px;padding:0 12px}@media (min-width: 1320px){.description-section{padding:0}}.description-section__header{display:grid;grid-gap:8px}.description-header__top{display:flex}.description-header-top__item{display:flex;grid-gap:4px;gap:4px;background-color:var(--neutral-light);padding:2px 8px;align-items:center;font-size:12px;line-height:16px;color:var(--primary)}.image-wrapper{display:flex;align-items:center}.colored-item{background:linear-gradient(90deg, var(--primary), var(--accent-red));color:#FFFFFF}.first{border-top-left-radius:2px;border-bottom-left-radius:2px}.last{border-top-right-radius:2px;border-bottom-right-radius:2px}.heading-container{position:relative}.heading{display:flex;text-align:justify;font-size:16px;line-height:24px;font-weight:400;color:var(--neutral-dark)}.heading-tags{position:absolute;right:0;bottom:4px;background-color:var(--neutral-grey)}@media (min-width: 1320px){.heading-tags{right:27%;bottom:5px}}.heading-tag{background-color:var(--neutral-grey-one);padding:2px 8px;font-size:12px;border-radius:2px}.reviews{display:grid;grid-template-columns:fit-content(50%) 1fr;grid-column-gap:24px}.rating{display:flex;grid-gap:4px;gap:4px}.rating-stars{display:flex;grid-gap:4px;gap:4px;color:var(--primary)}.buyers{display:flex;color:var(--neutral-dark)}.pricing{display:grid;grid-template-columns:fit-content(50%) 1fr;align-items:center;padding:12px 0;box-shadow:inset 0 1px 0 0 #E6E7EB, inset 0 -1px 0 0 #E6E7EB}@media (min-width: 1320px){.pricing{grid-template-columns:fit-content(50%) 1fr;grid-column-gap:12px}}.prices{display:flex;flex-direction:column;align-items:flex-start}.discounted-prices{font-size:16px;line-height:24px;font-weight:700;color:var(--primary)}.original-prices{display:flex;text-decoration:line-through}.pricing__text{display:flex;justify-content:right}@media (min-width: 1320px){.pricing__text{justify-content:left}}.pipe{color:#EDF0F3}@media (min-width: 1320px){.pipe{padding:0 6px}}.option-count{color:var(--neutral-dark)}.promo{display:grid;grid-template-columns:76px 1fr 24px;grid-gap:24px;gap:24px;height:40px;background-color:var(--neutral-light);align-items:center}.promo:hover{cursor:pointer}@media (min-width: 1320px){.promo{grid-template-columns:76px 164px 182px 24px;grid-gap:24px;gap:24px}}.promo-item{display:flex;justify-content:left;color:var(--primary)}.large-display{display:none}.logo{width:100%;height:100%;object-fit:contain}.chevron-container{align-items:center}.discount{display:grid;grid-template-columns:59px 1fr;align-items:center}.discount-amount{display:flex;color:var(--primary)}.discount-end{display:flex;grid-gap:12px;gap:12px;justify-content:left;align-items:center}.discount-timer{display:flex;grid-gap:12px;gap:12px;align-items:center;padding-left:12px;font-size:16px;line-height:24px;font-style:italic}.options-container{padding:12px 0}@media (min-width: 1320px){.options-container{display:grid;grid-template-columns:112px 1fr}}@media (min-width: 1320px){.large-display{display:flex}}.options{display:grid;grid-gap:10px;gap:10px}.option{display:grid;grid-template-columns:148px 1fr 120px;color:var(--neutral-dark);align-items:center;text-align:left}@media (min-width: 1320px){.option{grid-template-columns:180px 1fr 160px}}.description-section__footer{display:flex;flex-direction:column;grid-gap:12px;gap:12px;text-align:left;font-size:12px}.description-section__footer-item{display:flex;grid-gap:5px;gap:5px}.sidebar{padding:28px 26px 24px 26px;border-radius:8px;box-shadow:0 2px 8px 0 rgba(0,0,0,0.05)}@media (min-width: 1320px){.sidebar{height:-webkit-min-content;height:min-content;padding:28px 24px 24px 24px}}.added-products{display:grid;grid-template-rows:repeat(auto-fit, 32px);grid-row-gap:4px;width:100%;align-items:center;text-align:left}.added-product{display:grid;grid-template-columns:2fr 35px 70px;grid-gap:5px;gap:5px;padding-bottom:16px}.summary-section{display:flex;flex-direction:column;grid-gap:12px;gap:12px;text-align:left}.grid{display:grid;grid-template-columns:1fr 100px}.total-amount{display:flex;justify-content:right;font-size:16px;line-height:24px;font-weight:700;color:var(--neutral-dark)}.buttons{display:flex;flex-direction:column;grid-gap:12px;gap:12px;padding-top:12px}.bold{font-weight:700}.padding-top-12{padding:12px 0 0 0}.gap-4{grid-gap:4px;gap:4px}.gap-24{grid-gap:24px;gap:24px}.gap-28{grid-gap:28px;gap:28px}.flex{display:flex}.vertical-center{align-items:center}.underlined{text-decoration:underline}

